'use client';

import { useState, useEffect } from 'react';

interface GitHubUser {
  login: string;
  avatar_url: string;
  html_url: string;
  name: string;
  bio: string;
  public_repos: number;
  followers: number;
  following: number;
  created_at: string;
  location: string;
  blog: string;
  company: string;
}

interface GitHubRepo {
  id: number;
  name: string;
  description: string;
  html_url: string;
  language: string;
  stargazers_count: number;
  forks_count: number;
  updated_at: string;
  topics: string[];
}

export default function GitHubProfile() {
  const [userData, setUserData] = useState<GitHubUser | null>(null);
  const [repos, setRepos] = useState<GitHubRepo[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string>('');
  
  // Replace with your actual GitHub username
  const username = 'octocat'; // Change this to your GitHub username

  useEffect(() => {
    const fetchGitHubData = async () => {
      try {
        setLoading(true);
        
        // Fetch user data
        const userResponse = await fetch(`https://api.github.com/users/${username}`);
        if (!userResponse.ok) {
          throw new Error('Failed to fetch GitHub user data');
        }
        const user = await userResponse.json();
        setUserData(user);

        // Fetch repositories
        const reposResponse = await fetch(`https://api.github.com/users/${username}/repos?sort=updated&per_page=6`);
        if (!reposResponse.ok) {
          throw new Error('Failed to fetch GitHub repositories');
        }
        const repositories = await reposResponse.json();
        setRepos(repositories);
        
      } catch (err: any) {
        setError(err.message || 'An error occurred while fetching GitHub data');
      } finally {
        setLoading(false);
      }
    };

    fetchGitHubData();
  }, [username]);

  const formatDate = (dateString: string) => {
    const date = new Date(dateString);
    return date.toLocaleDateString('id-ID', {
      year: 'numeric',
      month: 'long'
    });
  };

  const getLanguageColor = (language: string) => {
    const colors: { [key: string]: string } = {
      JavaScript: 'bg-yellow-500',
      TypeScript: 'bg-blue-500',
      Python: 'bg-green-500',
      Java: 'bg-red-500',
      'C++': 'bg-blue-600',
      HTML: 'bg-orange-500',
      CSS: 'bg-blue-400',
      React: 'bg-cyan-500',
      Vue: 'bg-green-400',
      PHP: 'bg-purple-500',
    };
    return colors[language] || 'bg-gray-500';
  };

  if (loading) {
    return (
      <div className="max-w-6xl mx-auto">
        <div className="text-center mb-16">
          <h2 className="text-4xl md:text-5xl font-bold mb-4">
            <span className="text-blue-500">GitHub</span>{' '}
            <span className="text-slate-100">Profile</span>
          </h2>
          <div className="w-24 h-1 bg-gradient-to-r from-blue-500 to-green-500 mx-auto"></div>
        </div>
        
        <div className="flex items-center justify-center py-20">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-500"></div>
          <span className="ml-4 text-slate-400">Memuat data GitHub...</span>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="max-w-6xl mx-auto">
        <div className="text-center mb-16">
          <h2 className="text-4xl md:text-5xl font-bold mb-4">
            <span className="text-blue-500">GitHub</span>{' '}
            <span className="text-slate-100">Profile</span>
          </h2>
          <div className="w-24 h-1 bg-gradient-to-r from-blue-500 to-green-500 mx-auto"></div>
        </div>
        
        <div className="text-center py-20">
          <div className="text-red-500 text-lg mb-4">⚠️ {error}</div>
          <p className="text-slate-400">
            Silakan coba lagi nanti atau periksa koneksi internet Anda.
          </p>
        </div>
      </div>
    );
  }

  if (!userData) return null;

  return (
    <div className="max-w-6xl mx-auto">
      <div className="text-center mb-16">
        <h2 className="text-4xl md:text-5xl font-bold mb-4">
          <span className="text-blue-500">GitHub</span>{' '}
          <span className="text-slate-100">Profile</span>
        </h2>
        <div className="w-24 h-1 bg-gradient-to-r from-blue-500 to-green-500 mx-auto mb-6"></div>
        <p className="text-slate-400 text-lg">
          Lihat aktivitas dan kontribusi terbaru saya di GitHub
        </p>
      </div>

      {/* Profile Section */}
      <div className="grid lg:grid-cols-3 gap-8 mb-16">
        {/* Profile Card */}
        <div className="lg:col-span-1">
          <div className="bg-slate-800/30 rounded-2xl p-8 border border-slate-700 text-center">
            <div className="relative inline-block mb-6">
              <img
                src={userData.avatar_url}
                alt={userData.name || userData.login}
                className="w-32 h-32 rounded-full mx-auto border-4 border-blue-500/30"
              />
              <div className="absolute -bottom-2 -right-2 w-8 h-8 bg-green-500 rounded-full border-4 border-slate-800"></div>
            </div>
            
            <h3 className="text-2xl font-bold text-slate-100 mb-2">
              {userData.name || userData.login}
            </h3>
            <p className="text-blue-400 mb-4">@{userData.login}</p>
            
            {userData.bio && (
              <p className="text-slate-400 mb-6 leading-relaxed">
                {userData.bio}
              </p>
            )}
            
            <div className="space-y-3 mb-6 text-sm">
              {userData.location && (
                <div className="flex items-center justify-center text-slate-400">
                  <span className="mr-2">📍</span>
                  {userData.location}
                </div>
              )}
              {userData.company && (
                <div className="flex items-center justify-center text-slate-400">
                  <span className="mr-2">🏢</span>
                  {userData.company}
                </div>
              )}
              <div className="flex items-center justify-center text-slate-400">
                <span className="mr-2">📅</span>
                Bergabung {formatDate(userData.created_at)}
              </div>
            </div>
            
            <a
              href={userData.html_url}
              target="_blank"
              rel="noopener noreferrer"
              className="inline-flex items-center px-6 py-3 bg-blue-600 hover:bg-blue-700 text-white font-medium rounded-lg transition-colors"
            >
              Kunjungi GitHub
              <span className="ml-2">↗</span>
            </a>
          </div>
        </div>

        {/* Stats */}
        <div className="lg:col-span-2">
          <div className="grid grid-cols-3 gap-6 mb-8">
            <div className="bg-slate-800/30 rounded-xl p-6 border border-slate-700 text-center">
              <div className="text-3xl font-bold text-blue-500 mb-2">
                {userData.public_repos}
              </div>
              <div className="text-slate-400 text-sm">Repositories</div>
            </div>
            <div className="bg-slate-800/30 rounded-xl p-6 border border-slate-700 text-center">
              <div className="text-3xl font-bold text-green-500 mb-2">
                {userData.followers}
              </div>
              <div className="text-slate-400 text-sm">Followers</div>
            </div>
            <div className="bg-slate-800/30 rounded-xl p-6 border border-slate-700 text-center">
              <div className="text-3xl font-bold text-purple-500 mb-2">
                {userData.following}
              </div>
              <div className="text-slate-400 text-sm">Following</div>
            </div>
          </div>

          {/* GitHub Activity Visualization */}
          <div className="bg-slate-800/30 rounded-xl p-6 border border-slate-700">
            <h4 className="text-xl font-bold text-slate-100 mb-4">Aktivitas Kontribusi</h4>
            <div className="grid grid-cols-12 gap-1">
              {Array.from({ length: 84 }, (_, i) => (
                <div
                  key={i}
                  className={`w-3 h-3 rounded-sm ${
                    Math.random() > 0.7 ? 'bg-green-500' :
                    Math.random() > 0.5 ? 'bg-green-400' :
                    Math.random() > 0.3 ? 'bg-green-300' : 'bg-slate-700'
                  }`}
                  title={`Kontribusi hari ke-${i + 1}`}
                ></div>
              ))}
            </div>
            <div className="flex items-center justify-between mt-4 text-xs text-slate-400">
              <span>Less</span>
              <div className="flex items-center space-x-1">
                <div className="w-3 h-3 bg-slate-700 rounded-sm"></div>
                <div className="w-3 h-3 bg-green-300 rounded-sm"></div>
                <div className="w-3 h-3 bg-green-400 rounded-sm"></div>
                <div className="w-3 h-3 bg-green-500 rounded-sm"></div>
              </div>
              <span>More</span>
            </div>
          </div>
        </div>
      </div>

      {/* Recent Repositories */}
      <div>
        <h3 className="text-2xl font-bold text-slate-100 mb-8 text-center">
          Repository Terbaru
        </h3>
        
        <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6">
          {repos.map((repo) => (
            <div
              key={repo.id}
              className="bg-slate-800/30 rounded-xl p-6 border border-slate-700 hover:border-slate-600 transition-all duration-300 hover:transform hover:scale-105"
            >
              <div className="flex items-start justify-between mb-4">
                <h4 className="text-lg font-bold text-slate-100 hover:text-blue-400 transition-colors">
                  <a href={repo.html_url} target="_blank" rel="noopener noreferrer">
                    {repo.name}
                  </a>
                </h4>
                <a
                  href={repo.html_url}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="text-slate-400 hover:text-slate-300 transition-colors"
                >
                  ↗
                </a>
              </div>
              
              {repo.description && (
                <p className="text-slate-400 text-sm mb-4 line-clamp-2">
                  {repo.description}
                </p>
              )}
              
              <div className="flex items-center justify-between text-sm">
                <div className="flex items-center space-x-4">
                  {repo.language && (
                    <div className="flex items-center">
                      <div className={`w-3 h-3 rounded-full mr-2 ${getLanguageColor(repo.language)}`}></div>
                      <span className="text-slate-400">{repo.language}</span>
                    </div>
                  )}
                  <div className="flex items-center text-slate-400">
                    <span className="mr-1">⭐</span>
                    {repo.stargazers_count}
                  </div>
                  <div className="flex items-center text-slate-400">
                    <span className="mr-1">🍴</span>
                    {repo.forks_count}
                  </div>
                </div>
              </div>
              
              {repo.topics && repo.topics.length > 0 && (
                <div className="flex flex-wrap gap-2 mt-4">
                  {repo.topics.slice(0, 3).map((topic) => (
                    <span
                      key={topic}
                      className="px-2 py-1 bg-slate-700 text-slate-300 text-xs rounded-md"
                    >
                      {topic}
                    </span>
                  ))}
                </div>
              )}
              
              <div className="text-xs text-slate-500 mt-4">
                Updated {formatDate(repo.updated_at)}
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* CTA Section */}
      <div className="text-center mt-16">
        <p className="text-slate-400 mb-6">
          Ingin berkolaborasi atau berkontribusi pada proyek open source?
        </p>
        <div className="flex flex-col sm:flex-row gap-4 justify-center">
          <a
            href={userData.html_url}
            target="_blank"
            rel="noopener noreferrer"
            className="px-8 py-4 bg-gradient-to-r from-blue-600 to-green-600 text-white font-semibold rounded-lg hover:from-blue-700 hover:to-green-700 transition-all duration-300 transform hover:scale-105 shadow-lg"
          >
            Follow di GitHub
          </a>
          <a
            href="#contact"
            className="px-8 py-4 border-2 border-slate-600 text-slate-300 hover:border-slate-500 hover:text-white font-semibold rounded-lg transition-all duration-300"
          >
            Hubungi Saya
          </a>
        </div>
      </div>
    </div>
  );
}
