'use client';

import { useState, useEffect } from 'react';

export default function Hero() {
  const [displayText, setDisplayText] = useState('');
  const [currentIndex, setCurrentIndex] = useState(0);
  const [isDeleting, setIsDeleting] = useState(false);
  
  const titles = [
    'Fullstack Developer',
    'Web Developer',
    'API Developer',
    'React Specialist',
    'Next.js Expert'
  ];

  useEffect(() => {
    const currentTitle = titles[currentIndex];
    const timeout = setTimeout(() => {
      if (!isDeleting) {
        if (displayText.length < currentTitle.length) {
          setDisplayText(currentTitle.slice(0, displayText.length + 1));
        } else {
          setTimeout(() => setIsDeleting(true), 2000);
        }
      } else {
        if (displayText.length > 0) {
          setDisplayText(displayText.slice(0, -1));
        } else {
          setIsDeleting(false);
          setCurrentIndex((prev) => (prev + 1) % titles.length);
        }
      }
    }, isDeleting ? 50 : 100);

    return () => clearTimeout(timeout);
  }, [displayText, currentIndex, isDeleting, titles]);

  return (
    <div className="container mx-auto px-4 flex items-center justify-center min-h-screen">
      <div className="text-center max-w-4xl">
        {/* Greeting */}
        <div className="mb-8">
          <p className="text-lg md:text-xl text-slate-400 mb-2">Halo, saya</p>
          <h1 className="text-4xl md:text-6xl lg:text-7xl font-bold mb-4">
            <span className="text-blue-500">Developer</span>{' '}
            <span className="text-green-500">Indonesia</span>
          </h1>
        </div>

        {/* Animated Title */}
        <div className="mb-8 h-16 flex items-center justify-center">
          <h2 className="text-2xl md:text-3xl lg:text-4xl font-semibold text-slate-300">
            Saya seorang{' '}
            <span className="text-blue-400 border-r-2 border-blue-400 animate-pulse">
              {displayText}
            </span>
          </h2>
        </div>

        {/* Description */}
        <p className="text-lg md:text-xl text-slate-400 mb-12 max-w-3xl mx-auto leading-relaxed">
          Menciptakan solusi digital yang modern, responsif, dan user-friendly. 
          Berpengalaman dalam pengembangan web fullstack dengan teknologi terkini 
          seperti React, Next.js, Node.js, dan berbagai database.
        </p>

        {/* CTA Buttons */}
        <div className="flex flex-col sm:flex-row gap-4 justify-center items-center">
          <a
            href="#projects"
            className="px-8 py-4 bg-blue-600 hover:bg-blue-700 text-white font-semibold rounded-lg transition-all duration-300 transform hover:scale-105 shadow-lg hover:shadow-blue-500/25"
          >
            Lihat Proyek Saya
          </a>
          <a
            href="#about"
            className="px-8 py-4 border-2 border-green-500 text-green-500 hover:bg-green-500 hover:text-white font-semibold rounded-lg transition-all duration-300 transform hover:scale-105"
          >
            Tentang Saya
          </a>
        </div>

        {/* Scroll Indicator */}
        <div className="absolute bottom-8 left-1/2 transform -translate-x-1/2 animate-bounce">
          <div className="w-6 h-10 border-2 border-slate-400 rounded-full flex justify-center">
            <div className="w-1 h-3 bg-slate-400 rounded-full mt-2 animate-pulse"></div>
          </div>
        </div>
      </div>

      {/* Background Elements */}
      <div className="absolute inset-0 overflow-hidden pointer-events-none">
        <div className="absolute top-1/4 left-1/4 w-64 h-64 bg-blue-500/10 rounded-full blur-3xl"></div>
        <div className="absolute bottom-1/4 right-1/4 w-64 h-64 bg-green-500/10 rounded-full blur-3xl"></div>
        <div className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 w-96 h-96 bg-slate-800/20 rounded-full blur-3xl"></div>
      </div>
    </div>
  );
}
